:root {
  --background-color: #84a178;
  --first-text-color: #fcfcfc;
  --second-text-color: #131313;
  --white: #ffffff;
  --yellow: #f8d24e;
  --green: #0a8817;
  --grey: #7a7a7a;
  --lite-yellow: #efd478;
  --lite-green: #aec6a5;
  --lite-orange: #d6936d;
  --lite-gray: #ababab;
}

//
$mobile: 480px;
$tablet: 768px;
$desktop: 1200px;

// primary-bg-color -   
$primary-bg-color: #84a178;

//        
$first-cards-color: #aec6a5;

//        
$second-cards-color: #efd478;

//        
$third-cards-color: #d6936d;

//  , ,  ,   
$accent-color: #f8d24e;

//    -    ,
//       about product, buy now
$decor-elements-color: #9cbe8f;

//     
$hover-color: #0a8817;

//     
$disabled-color: #ababab;

//          (   )
$primary-text-color: #131313;

//          (   ),    ,     
$white-text-color: #fcfcfc;

// gray-text-color -     
$gray-text-color: #ababab;

//     
$copyright-color: #7a7a7a;

//    
$focus-color: #ffffff;

//    
$border-color: #e1e1e1;

//  
$main-fon: 'Montserrat', sans-serif;

//  
$secondary-fon: 'Seymour One', sans-serif;

//   
$transition-duration: 300ms;
$timing-function: cubic-bezier(0.4, 0, 0.2, 1);
$transition-timing-function: $timing-function;

//
$shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;

@mixin mq($type) {
  $phone: 480px;
  $tablet: 768px;
  $desktop: 1200px;

  @if $type == 'phone' {
    @media screen and (min-width: $phone) {
      @content;
    }
  }

  @if $type == 'phone-only' {
    @media screen and (max-width: #{$tablet - 0.02}) {
      @content;
    }
  }

  @if $type == 'tablet' {
    @media screen and (min-width: $tablet) {
      @content;
    }
  }

  @if $type == 'tablet-only' {
    @media screen and (max-width: #{$desktop - 0.02}) {
      @content;
    }
  }

  @if $type == 'desktop' {
    @media screen and (min-width: $desktop) {
      @content;
    }
  }
}
